<template>
  <div :id="id"></div>
</template>

<script>
  import echarts from 'echarts'
  import { mapActions } from 'vuex'
  
  export default {
    name: 'baseChart',
    props: {
      id: {
        type: String,
        default: 'chart'
      },
      option: {
        type: Object
      }
    },
    data() {
      return {
        chart: null
      }
    },
    mounted() {
      // this.initChart()
    },
    beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose && this.chart.dispose()
      this.chart = null
      this.delResizeFn(this.id)
    },
    methods: {
      ...mapActions(['addResizeFn']),
      ...mapActions(['delResizeFn']),
      initChart() {
        this.chart = echarts.init(document.getElementById(this.id))
        this.chart.setOption(this.option)
				this.addResizeFn({
					key: this.id,
					fn: this.resizeChart.bind(this)
				})
      },
      updateChart(option) {
        if (this.chart !== null) {
          const old_option = this.chart.getOption()
          this.chart.setOption(Object.assign({}, old_option, option))
        }
      },
      resizeChart() {
        this.chart.resize && this.chart.resize()
      }
    }
  }
</script>
<style scoped>
  div{
    width: 100%;
    height: 100%;
  }
</style>
