<template>
  <div class="home">
	  	<!-- 顶部标题 -->
		<header>
			<div class="header-left">
				<header>天麓湖森林公园环境DCP监测平台</header>
				<img src="./../assets/bg_headline.png"/>
			</div>
			<p class="header-right">{{time}}</p>
		</header>

		<main>
			<!-- 测试内容-start -->
			<ul class="marker-test">
				<li class="marker-test-item" v-for="(device, index) in list" @mouseenter="showDevice(index)" @mouseleave="hideDevice()" @click="goDeviceInfo(index)">
					<div class="DCP-info" v-show="activeIndex === index">
						<p class="DCP-info-header">
							<span>设备ID:{{device.deviceID}}</span>
							<span class="DCP-info-header-status" v-if="device.status === 1">正常</span>
							<span class="DCP-info-header-status fail" v-if="device.status !== 1">异常</span>
						</p>
						<ul class="DCP-info-list">
							<li>温度 {{device.temp}}°C</li>
							<li>时雨量 {{device.rainHour}}mm</li>
							<li>湿度 {{device.humidity}}%</li>
							<li>风速 {{device.windSpeed}}m/s</li>
							<li>气压 {{device.pressure}}Pa</li>
							<li>水位 {{device.waterLevel}}m</li>
						</ul>
					</div>
				</li>
			</ul>
			<!-- 测试内容-end -->

			<!-- 天麓湖地图 -->
			<div id="map"></div>
			<!-- 飞线容器 -->
			<dv-full-screen-container style="width:100%;height:100%;z-index:2;">
				<dv-flyline-chart-enhanced :config="config" :dev="true" style="width:100%;height:100%;" />
			</dv-full-screen-container>
			<!-- 卫星设备 -->
			<img class="satellite" src="./../assets/map_wx.png" alt="卫星" />
			<!-- 右侧信息栏 -->
			<section class="right-info">
				<!-- 设备 -->
				<div class="device-info">
					<p class="info-title">监测设备概览</p>
					<div class="device-info-box">
						<div class="device-info-box-top">
							<p class="box-header m-size">监测设备</p>
							<div class="box-content">
								<div class="box-panel">
									<span>100</span><span style="font-size:16px">%</span>
									<svg xmlns="http://www.w3.org/200/svg" height="120" width="120">
										<circle cx="60" cy="60" :r="r" fill="none" stroke="#43bafe" stroke-width="15" :stroke-dasharray="ciecleValue" />
									</svg>
									<img src="./../assets/dial_01.png" alt="图片" />
								</div>
								<div class="box-status">
									<p class="box-num"> <span class="green-text">5</span> <span class="s-size">台</span></p>
									<p class="m-size">正常设备</p>
								</div>
								<div class="box-status">
									<p class="box-num"> <span class="red-text">5</span> <span class="s-size">台</span></p>
									<p class="m-size">异常设备</p>
								</div>
							</div>
						</div>
						<div class="device-info-box-bottom">
							<p class="box-header m-size">监测数据</p>
							<div class="box-content">
								<div class="box-status">
									<p class="box-num"> <span class="skygreen-text">1,568</span> <span class="s-size">条</span></p>
									<p class="m-size">今日发送报文</p>
								</div>
								<div class="box-status">
									<p class="box-num"> <span class="blue-text">1,568</span> <span class="s-size">条</span></p>
									<p class="m-size">累计发送报文</p>
								</div>
							</div>
						</div>
					</div>
				</div>
				<!-- 终端 -->
				<div class="terminal-info">
					<p class="info-title">终端数据监测</p>
					<div class="terminal-info-box">
						<p class="terminal-info-box-header">更新时间：2020年10月25日 10:20</p>
						<ul class="terminal-info-box-list">
							<li>
								<p>26 <span class="s-size">°C</span></p>
								<p class="m-size">最高温度</p>
							</li>
							<li>
								<p>50 <span class="s-size">%</span><p>
								<p class="m-size">最低湿度</p>
							</li>
							<li>
								<p>80 <span class="s-size">mm</span></p>
								<p class="m-size">最大时雨量</p>
							</li>
							<li>
								<p>1.9 <span class="s-size">m/s</span></p>
								<p class="m-size">最大风速</p>
							</li>
							<li>
								<p>1007.2 <span class="s-size">hPa</span></p>
								<p class="m-size">最高气压</p>
							</li>
							<li>
								<p>3.8 <span class="s-size">m</span></p>
								<p class="m-size">最高水位</p>
							</li>
						</ul>
					</div>
				</div>
			</section>

			<!-- loading动画 -->
			<Loading :loading="loading"></Loading>
		</main>
  </div>
</template>

<script>
import Loading from '@/components/loading/index.vue'
import { gcj02towgs84 } from '@/utils/formatLonLat.js'
import { parseTime } from '@/utils/formatTime.js'

export default {
	// name: 'map',
	data() {
		return {
			// 测试内容-start
			activeIndex: -1, // 当前hover设备下标
			// 测试内容-end

			time: "", // 时间
			// 飞线配置
			config:{
				line: {
					color: '#68ffc1',
					width: 2,
				},
				points: [
					{ name: '卫星', coordinate: [0.12, 0.16] },
					{ name: '设备1', coordinate: [0.113, 0.640], halo: { show: true, color: '#68ffc1' } },
					{ name: '设备2', coordinate: [0.195, 0.468], halo: { show: true, color: '#68ffc1' } },
					{ name: '设备3', coordinate: [0.347, 0.485], halo: { show: true, color: '#68ffc1' } },
					{ name: '设备4', coordinate: [0.320, 0.227], halo: { show: true, color: '#68ffc1' } },
					{ name: '设备5', coordinate: [0.512, 0.330], halo: { show: true, color: '#68ffc1' } },
				],
				lines: [
					{ source: '设备1', target: '卫星' },
					{ source: '设备2', target: '卫星' },
					{ source: '设备3', target: '卫星' },
					{ source: '设备4', target: '卫星' },
					{ source: '设备5', target: '卫星' }
				],
				k: -0.5,
			},
			r: 51, // 环形半径
			panelValue: 0.35, // 环形图百分比
			map: null,
			loading: false,
			popup: null,
			list: [
				// deviceID:设备id, lon/lat:设备经/纬度, status:状态(1:正常,0:异常), 113.425559,23.220023
				{ deviceID: '1', lon: 113.413597, lat: 23.216547, status: 1, temp: 26, rainHour: 33, humidity: 80, windSpeed: 1.9, pressure: 1000.0, waterLevel: 3.85 },
				{ deviceID: '2', lon: 113.416559, lat: 23.222323, status: 0, temp: 27, rainHour: 33, humidity: 56, windSpeed: 2.3, pressure: 1005.6, waterLevel: 3.75 },
				{ deviceID: '3', lon: 113.421828, lat: 23.222158, status: 1, temp: 28, rainHour: 30, humidity: 50, windSpeed: 1.8, pressure: 1004.3, waterLevel: 3.65 },
				{ deviceID: '4', lon: 113.420514, lat: 23.229661, status: 0, temp: 30, rainHour: 32, humidity: 60, windSpeed: 3.2, pressure: 1001.5, waterLevel: 3.25 },
				{ deviceID: '5', lon: 113.429405, lat: 23.226702, status: 1, temp: 28, rainHour: 30, humidity: 80, windSpeed: 1.9, pressure: 1007.2, waterLevel: 3.05 }
			],
		}
	},
	components: {
		Loading,
	},
	mounted() {
		this.popup = new mapboxgl.Popup({
			closeButton: false,
			closeOnClick: false,
			offset: [100, 20], //right top
		});
		this.time = parseTime(new Date(), '{y}-{m}-{d} {h}:{i}')
		// 加载地图
		// this.initMapbox()
	},
	methods: {
		// 测试内容-start
		showDevice(index){
			this.activeIndex = index;
		},
		hideDevice(index){
			this.activeIndex = -1;
		},
		goDeviceInfo(index){
			this.hideDevice();
			this.$router.push({
				name: 'dataStatistics',
				params: {
					"deviceInfo": this.list[index]
				}
			})
		},
		// 测试内容-end



		// 初始化地图
		initMapbox() {
			this.loading = true
			mapboxgl.accessToken = 'pk.eyJ1IjoiaGVxaW5taW5nIiwiYSI6ImNrZ3N0bzFobjE0bGIyd21saW1sejZjZjQifQ.E2EIxdX3qgOfcta3bqUlow';
			const bounds = [
				[113.4097147797331, 23.213288767237334], // Southwest coordinates
				[113.4344387648625, 23.23786527738054]  // Northeast coordinates
			];
			this.map = new mapboxgl.Map({
				container: 'map',
				style: 'mapbox://styles/heqinming/ckh1sian308sw19ofdszultwz',
				center: [113.42615729888844,23.22333856771808],
				zoom: 14.5,
				interactive: false,
				pitch: 45,  // 50
				bearing: 0, // 30
				// maxBounds: bounds // Sets bounds as max
			})
			// 将地图语言设置为中文(使用mapbox插件更换语言，因兼容问题暂不使用)
			// mapboxgl.setRTLTextPlugin(
			//   "https://api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-rtl-text/v0.10.1/mapbox-gl-rtl-text.js"
			// );
			// this.setLanguage('zh')
			// 隐藏mapboxLogo
			let logo = document.getElementsByClassName('mapboxgl-ctrl-logo')
			for (let i = 0; i < logo.length; i++) {
				logo[i].style.display = 'none'
			}
			this.map.on('load', () => {
				// 将地图设置为中文
				this.setLanguage()
				// 添加3D建筑
				this.add3DBuilding()
				// 添加标记点
				this.addMarker(this.list);
			})
			this.map.on('zoomend', (e) => {
			})
		},
		// 地图语言设置为中文
		setLanguage() {
			let changeLg = []
			let layerList = this.map.getStyle()
			layerList.layers.forEach(item => {
				let index = item.id.indexOf('-label')
				if (index > -1) {
					changeLg.push(item.id)
				}
			})
			changeLg.forEach(item => {
				this.map.setLayoutProperty(item, 'text-field', [
					'format',
					['get', 'name-zh'],
					{ 'font-scale': 1.2 },
					'\n',
					{},
					['get', 'name'],
					{
						'font-scale': 0.8,
						'text-font': ['literal',['DIN Offc Pro Italic', 'Arial Unicode MS Regular']]
					}
				])
			})
			this.loading = false;  // 修复loadingbug
			// 也可以使用mapbox的语言类插件，但该插件目前不支持使用版本的地图
			// let language = new MapboxLanguage({ defaultLanguage: type })
			// this.map.addControl(language)
		},
		// 添加3D建筑
		add3DBuilding() {
			var layers = this.map.getStyle().layers;
			var labelLayerId;
			for (var i = 0; i < layers.length; i++) {
				if (layers[i].type === 'symbol' && layers[i].layout['text-field']) {
				labelLayerId = layers[i].id;
				break;
				}
			}
			this.map.addLayer({
				'id': '3d-buildings',
				'source': 'composite',
				'source-layer': 'building',
				'filter': ['==', 'extrude', 'true'],
				'type': 'fill-extrusion',
				'minzoom': 15,
				'paint': {
				'fill-extrusion-color': '#aaa',

				'fill-extrusion-height': ["*", ["get", "height"], 10],
				'fill-extrusion-base': [
					"interpolate", ["linear"], ["zoom"],
					15, 0,
					15.05, ["get", "min_height"]
				],
				'fill-extrusion-opacity': 1
				}
			});
			// , labelLayerId
		},
		// 添加模拟数据marker标记
		addMarker(data) {
			this.marker = []
			data.forEach((item, index) => {
				// let lnglat = gcj02towgs84(item.lon, item.lat)
				let content = this.createMarkerContent(item)
				let marker = new mapboxgl.Marker({
					draggable: false,
					element: content,
					offset: [0, -30],
				}).setLngLat([item.lon, item.lat]).addTo(this.map)
				this.marker.push(marker)
			})
			this.showMarker = true
			this.map.on('zoomend', (eve) => {
				// 当地图层级低于11时，隐藏marker
				if (this.map.getZoom() >= 11) {
					if (this.showMarker) {
						return
					}
					this.marker.forEach(item => {
						item.addTo(this.map)
					})
					this.showMarker = true
				} else {
					if (!this.showMarker) {
						return
					}
					this.marker.forEach(item => {
						item.remove()
					})
					this.showMarker = false
				}
			})
		},
		// 创建标记点内容
		createMarkerContent(item) {
			let lnglat = gcj02towgs84(item.lon, item.lat)
			let img = document.createElement('img')
			img.src = require("./../assets/location2.png")
			img.style.width = 30 + 'px'
			img.style.height = 30 + 'px'
			img.style.zIndex = 3
			img.style.marginLeft = 20 + 'px'
			img.addEventListener('click', () => {
				this.popup.remove()
				this.$router.push({
					path: '/dataStatistics'
				})
			})
			img.addEventListener('mouseenter', (e) => {
				this.handlePopup(item)
			})
			img.addEventListener('mouseleave', (e) => {
				this.popup.remove()
			})
			return img
		},
		// 显示/隐藏pop提示
		handlePopup(data) {
			let lnglat = gcj02towgs84(data.lon, data.lat);
			let statusHtml = '<span class="DCP-info-header-status">正常</span>';
			if(data.status !== 1){
				statusHtml = '<span class="DCP-info-header-status fail">异常</span>'
			}
			let conetntHtml = `
				<div class="DCP-info">
					<p class="DCP-info-header">
						<span>设备ID:${data.deviceID}</span>
						${statusHtml}
					</p>
					<ul class="DCP-info-list">
						<li>温度 ${data.temp}°C</li>
						<li>时雨量 ${data.rainHour}mm</li>
						<li>湿度 ${data.humidity}%</li>
						<li>风速 ${data.windSpeed}m/s</li>
						<li>气压 ${data.pressure}Pa</li>
						<li>水位 ${data.waterLevel}m</li>
					</ul>
				</div>
			`
			this.popup.setLngLat(lnglat)
			.setHTML(conetntHtml)
			.addTo(this.map)
		},

	},
	computed: {
		ciecleValue(){
			// 2*3.14*51 = 320.28
			return this.panelValue * 2 * 3.14 * this.r + ",10000"
		}
	}
}
</script>

