// 解决vue中监听window.resize事件的问题
const resize = {
  state: {
    resizeFn: {} // 事件队列
  },

  mutations: {
    Add_resizeFn: (state, opt) => {
      state.resizeFn[opt.key] = opt.fn
    },
    Del_resizeFn: (state, key) => {
      delete state.resizeFn[key]
    }
  },

  actions: {
    addResizeFn({ commit }, opt) {
      commit('Add_resizeFn', opt)
    },
    delResizeFn({ commit }, key) {
      commit('Del_resizeFn', key)
    }
  }
}

export default resize
