import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import '@/icons' // icon
import dataV from '@jiaminghi/data-view'

Vue.use(dataV)
Vue.config.productionTip = false

const vm = new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// 窗口大小改变自适应
window.onresize = () => {
  for (const key in vm.$store.getters.resize.resizeFn) {
    vm.$store.getters.resize.resizeFn[key]()
  }
}
